<template>
  <v-container fill-height fluid>
    <v-row align="center" justify="center">
      <v-card class="elevation-24 card tarjeta" style="width: 90%">
        <v-window v-model="step">
          <v-window-item :value="1">
            <v-row align="center" justify="center">
              <v-col cols="12" md="8" class="text-center">
                <v-form
                  class="signup-form-form ml-6 mr-6 mt-10 mb-10 pl-10 pr-10"
                >
                    <h1
                    
                      class="text-center darken-3--text mb-10  font-weight-bold color_h1"
                    >
                      INICIA SESIÓN
                    </h1>

                  <v-text-field
                    class="icon"
                    id="email"
                    v-model="emailLogin"
                    label="Nombre de usuario"
                    name="email"
                    append-icon="mdi-email-outline"
                    type="text"
                    required
                  />
                  <v-text-field
                    class="icon"
                    id="password"
                    v-model="passwordLogin"
                    label="Contraseña"
                    name="Password"
                    :append-icon="icon_eye"
                    @click:append="switchPassword"
                    :type="showPass"
                    required
                  />
                  <div class="text-center mt-9">
                    <v-btn icon outlined large style="margin-right: 30px"
                      ><v-avatar size="35px">
                        <img src="../assets/Google-Logo.png" /> </v-avatar
                    ></v-btn>
                    <v-btn icon outlined large
                      ><v-avatar size="35px">
                        <img src="../assets/Facebook-Logo.png" /> </v-avatar
                    ></v-btn>
                    <v-btn icon outlined large style="margin-left: 30px"
                      ><v-avatar size="35px">
                        <img src="../assets/Twitter-Logo.png" /> </v-avatar
                    ></v-btn>
                  </div>
                  <div class="text-center mt-12">
                    <v-btn
                      @click="signin"
                      large
                      dark
                      style="margin-right: 30px"
                      color="primary"
                      >Iniciar sesión</v-btn
                    >
                    <v-btn @click="changeWindow" large dark color="primary"
                      >Registrarse</v-btn
                    >
                  </div>
                </v-form>
              </v-col>
              <v-col
                cols="12"
                md="4"
                style="max-width:100% min-height:100%"
                class="particle"
              >
                <Particle
                  msg="BIENVENIDO"
                  :contador_particulas="contadorParticulas"
                />
              </v-col>
            </v-row>
          </v-window-item>
          <v-window-item :value="2">
            <v-row align="center" justify="center">
              <v-col
                cols="12"
                md="4"
                style="max-width:100% min-height:100%"
                class="particle"
              >
                <Particle
                  msg="Vamos allá!"
                  :contador_particulas="contadorParticulas"
                />
              </v-col>
              <v-col cols="12" md="8" class="text-center">
                <v-form class="signup-form-form ml-6 mr-6 pl-10 pr-10">
                  <h1 class="text-center   font-weight-bold mb-2 color_h1">
                    REGÍSTRATE
                  </h1>
                  <v-text-field
                    class="icon"
                    id="name"
                    v-model="name"
                    label="Nombre"
                    name="name"
                    append-icon="mdi-account-outline"
                    type="text"
                    required
                    :error-messages="nameErrors"
                    @input="$v.name.$touch()"
                    @blur="$v.name.$touch()"
                  />
                  <v-text-field
                    class="icon"
                    id="username"
                    v-model="username"
                    label="Nombre de usuario"
                    name="username"
                    append-icon="mdi-smart-card-outline"
                    type="text"
                    required
                    :error-messages="usernameErrors"
                    @input="$v.username.$touch()"
                    @blur="$v.username.$touch()"
                  />
                  <v-text-field
                    class="icon"
                    id="emailRegister"
                    v-model="emailRegister"
                    label="Correo electrónico"
                    name="email"
                    append-icon="mdi-email-outline"
                    type="email"
                    required
                    :error-messages="emailRegisterErrors"
                    @blur="$v.emailRegister.$touch()"
                  />
                  <v-text-field
                    class="icon"
                    id="passwordRegister"
                    v-model="passwordRegister"
                    label="Contraseña"
                    name="Password"
                    :append-icon="icon_eye"
                    @click:append="switchPassword"
                    :type="showPass"
                    required
                    :error-messages="passwordRegisterErrors"
                    @blur="$v.passwordRegister.$touch()"
                  />
                  <div style="display: inline" v-if="showBars">
                    <v-btn
                      style="height: 10px; margin-right: 5px; margin-top: -20px"
                      class="bar"
                      elevation="0"
                      color="white"
                    ></v-btn>
                    <v-btn
                      style="height: 10px; margin-right: 5px; margin-top: -20px"
                      class="bar"
                      elevation="0"
                      color="white"
                    ></v-btn>
                    <v-btn
                      style="height: 10px; margin-right: 5px; margin-top: -20px"
                      class="bar"
                      elevation="0"
                      color="white"
                    ></v-btn>
                    <v-btn
                      style="height: 10px; margin-top: -20px"
                      class="bar"
                      elevation="0"
                      color="white"
                    ></v-btn>
                  </div>
                  <v-text-field
                    class="icon"
                    id="passwordRepeat"
                    v-model="passwordRepeat"
                    label="Confirmar contraseña"
                    name="Password"
                    :append-icon="icon_eye"
                    @click:append="switchPassword"
                    :type="showPass"
                    v-bind:class="{ showBars: showBars }"
                    required
                    :error-messages="passwordRepeatErrors"
                    @blur="$v.passwordRepeat.$touch()"
                  />
                  <div class="text-center mt-4 mb-4">
                    <v-btn icon outlined large style="margin-right: 30px"
                      ><v-avatar size="35px">
                        <img src="../assets/Google-Logo.png" /> </v-avatar
                    ></v-btn>
                    <v-btn icon outlined large
                      ><v-avatar size="35px">
                        <img src="../assets/Facebook-Logo.png" /> </v-avatar
                    ></v-btn>
                    <v-btn icon outlined large style="margin-left: 30px"
                      ><v-avatar size="35px">
                        <img src="../assets/Twitter-Logo.png" /> </v-avatar
                    ></v-btn>
                  </div>
                  <div class="text-center mt-6">
                    <v-btn
                      @click="changeWindow"
                      large
                      dark
                      style="margin-right: 30px"
                      color="primary"
                      >Iniciar sesión</v-btn
                    >
                    <v-btn @click="signup" large dark color="primary"
                      >Registrarse</v-btn
                    >
                  </div>
                </v-form>
              </v-col>
            </v-row>
          </v-window-item>
        </v-window>
      </v-card>
    </v-row>
  </v-container>
</template>

<style scoped>

.icon {
  padding-left: 60px;
  padding-right: 60px;
  width: 100%
}

.particle {
  padding: 0;
}
.tarjeta {
  height: 600px;
}

.color_h1 {
  color: #13314a;
  
  font-size: 3em;
  
}

.showBars {
  margin-top: -20px;
}

.bar:hover {
  color: transparent;
}

.bar:focus {
  color: transparent;
}

.icon >>> .v-icon {
  font-size: 2em;
  color: #13314a;
}

.icon >>> .v-label {
  color: #13314a;
  font-size: 1.1em;
}

.icon >>> input {
  font-size: 1.2em;
}
</style>

<script>
import Particle from "../components/Particle.vue";
import { mapState, mapMutations } from "vuex";
import { validationMixin } from "vuelidate";
import {
  required,
  maxLength,
  minLength,
  email,
  sameAs,
} from "vuelidate/lib/validators";

export default {
  name: "SignIn",
  mixins: [validationMixin],
  validations: {
    name: { required, maxLength: maxLength(30) },
    username: {
      required,
      maxLength: maxLength(15),
    },
    emailRegister: { required, email },
    passwordRegister: { required, minLength: minLength(8) },
    passwordRepeat: { required, sameAsPassword: sameAs("passwordRegister") },
  },
  mounted() {
    this.step = 1;
  },
  created() {
    window.addEventListener("resize", this.myEventHandler);
  },
  destroyed() {
    window.removeEventListener("resize", this.myEventHandler);
  },
  components: {
    Particle,
  },
  data: () => ({
    step: 2,
    name: "",
    username: "",
    emailLogin: "",
    emailRegister: "",
    passwordLogin: "",
    passwordRegister: "",
    showPass: "password",
    passwordRepeat: "",
    password: "",
    contadorParticulas: 80,
    showBars: false,
  }),
  computed: {
    icon_eye() {
      if (this.showPass == "password") {
        return "mdi-eye-outline";
      } else {
        return "mdi-eye-off-outline";
      }
    },
    nameErrors() {
      const errors = [];
      if (!this.$v.name.$dirty) return errors;
      !this.$v.name.maxLength &&
        errors.push("El nombre debe tener como máximo 30 caracteres.");
      !this.$v.name.required && errors.push("Campo requerido.");
      return errors;
    },
    usernameErrors() {
      const errors = [];
      if (!this.$v.username.$dirty) return errors;
      !this.$v.username.maxLength &&
        errors.push(
          "El nombre de usuario debe tener como máximo 15 caracteres."
        );
      !this.$v.username.required && errors.push("Campo requerido.");
      return errors;
    },
    emailRegisterErrors() {
      const errors = [];
      if (!this.$v.emailRegister.$dirty) return errors;
      !this.$v.emailRegister.email &&
        errors.push("El email debe tener un formato válido.");
      !this.$v.emailRegister.required && errors.push("Campo requerido.");
      return errors;
    },
    passwordRegisterErrors() {
      const errors = [];
      if (!this.$v.passwordRegister.$dirty) return errors;
      !this.$v.passwordRegister.minLength &&
        errors.push("La contraseña debe tener como mínimo 8 caracteres.");
      !this.$v.passwordRegister.required && errors.push("Campo requerido.");
      return errors;
    },
    passwordRepeatErrors() {
      const errors = [];
      if (!this.$v.passwordRepeat.$dirty) return errors;
      !this.$v.passwordRepeat.sameAsPassword &&
        errors.push("Las dos contraseñas deben ser iguales");
      !this.$v.passwordRepeat.required && errors.push("Campo requerido.");
      return errors;
    },
    ...mapState(["logged"]),
  },
  watch: {
    passwordRegister() {
      if (this.passwordRegister.length > 0) {
        this.showBars = true;
      } else {
        this.showBars = false;
      }
    },
  },
  methods: {
    switchPassword() {
      if (this.showPass == "password") {
        this.showPass = "text";
      } else {
        this.showPass = "password";
      }
    },
    signin() {
      if (this.emailLogin != "" && this.passwordLogin != "") {
        this.login(this.emailLogin);
        this.setPassword(this.passwordLogin)
        this.$router.push("/");
      }
    },
    signup() {
      this.$v.$touch();
      if (this.$v.$invalid) {
      } else {
        this.setEmail(this.emailRegister)
        this.setName(this.name)
        this.changeWindow();
      }
    },
    changeWindow() {
      this.$v.$reset();
      if (this.step == 1) {
        this.step = 2;
      } else {
        this.step = 1;
      }
      this.name = "";
      this.username = "";
      this.emailLogin = "";
      this.passwordLogin = "";
      this.showPass = "password";
      this.emailRegister = "";
      this.passwordRegister = "";
      this.passwordRepeat = "";
      this.contadorParticulas++;
    },
    validateEmail(inputText) {
      var mailformat = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
      if (inputText.match(mailformat)) {
        return true;
      } else {
        return false;
      }
    },
    myEventHandler(e) {
    },
    ...mapMutations(["login", "setEmail", "setName", "setPassword"]),
  },
};
</script>
